      height: 55vh;
      position: absolute;
      top: 0;
      left: 0; }
    .warp .warp-bg img:nth-of-type(1) {
      animation: fadeInOut 8s linear 6s infinite; }
    .warp .warp-bg img:nth-of-type(2) {
      animation: fadeInOut 8s linear 4s infinite; }
    .warp .warp-bg img:nth-of-type(3) {
